.project-preview-container {
    width: 100%;
    height: 40%;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.project-preview-container .project-preview-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2.5rem;
}

@media screen and (max-width : 500px) {
    .project-preview-container {
        height: 50%;
    }

    .project-preview-container .project-preview-overlay {
        padding: 0 .5rem;
        font-size: 2rem;
        text-align: center;
    }
    .project-preview-container .project-preview-overlay div:first-of-type {
        width: 100%;
    }
}
